n-name: zoom; \n            animation-name: zoom;\n    -webkit-animation-duration: 0.6s;\n            animation-duration: 0.6s;\n  }\n@-webkit-keyframes zoom {\n    from {-webkit-transform:scale(0);transform:scale(0)} \n    to {-webkit-transform:scale(1);transform:scale(1)}\n  }\n@keyframes zoom {\n    from {-webkit-transform:scale(0);transform:scale(0)} \n    to {-webkit-transform:scale(1);transform:scale(1)}\n  }\n/* The Close Button */\n.close {\n    position: absolute;\n    top: 15px;\n    right: 35px;\n    color: #f1f1f1 !important;\n    font-size: 40px;\n    font-weight: bold;\n    transition: 0.3s;\n  }\n.close:hover,\n  .close:focus {\n    color: #bbb;\n    text-decoration: none;\n    cursor: pointer;\n  }\n/* 100% Image Width on Smaller Screens */\n@media only screen and (max-width: 700px){\n    .modal-content {\n      width: 100%;\n    }\n  }\n/* Processing spinner */\n.processing-change {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    width: 25%;\n    height: 25%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-content: center;\n    align-items: center;\n    -webkit-transform: translate(-50%, -50%);\n            transform: translate(-50%, -50%);\n    background-color: #000;\n    opacity: .5;\n    border-radius: .25rem;\n    z-index: 1000;\n}\n.processing-change p {\n    color: white;\n}\n/*navigation buttons*/\n#next,\n#previous {\n    position: relative;\n    top: -55%;\n    font-size: 2em;\n    color: white;\n    text-shadow: 0 1px 0 #fff;\n    opacity: .5;\n    display: inline;\n}\n#next {\n    float: right;   \n    margin-right: 5em;\n}\n#previous {\n    float: left;\n    margin-left: 5em;\n}\n/*overlay*/\n.overlay {\n    position: absolute;\n    /* width: 100%; */\n    bottom: 0em;\n    right: 2em;\n    /* display: flex; */\n    /* flex-direction: column; */\n    /* align-items: center; */\n    /* justify-content: center; */\n    font-size: 28px;\n    color: #6c757d;\n    opacity: .5;\n}\n/*# so